.navBar{
    background-color: rgb(0,0,0,0.4);
    display: flex;
    /* justify-content: center; */
    align-items: center;
    position: fixed;
    width: 100%;
    top: 0px;
    height: 80px;
    z-index: 70;
}
.navBar.active{
    background-color: #121111;
}

