/* CSS to logo  */
.navBar img{
    margin: 5px 10px;
}
.logo{
    margin-left: 40px;
}
#iitk-logo{
    width: 60px;
    /* filter: invert(1); */
}
#aero-logo{
    width: 62px;
    /* filter: invert(1); */
}

/* CSS to menu  */
.menu{
    position: absolute;
    right: 150px;
    font-family: Ubuntu;
    font-weight: bolder;
}
.menu-option{
    display: flex;
    list-style: none;
}
.nav-item{
    margin: 0 30px;
    font-size: 20px;
}
.link{
    text-decoration: none;
    color: white;
}